<template lang="html">
  <div id="new-employee">
    <h3>New Employee</h3>
    <div class="row">
      <form v-on:submit.prevent="saveEmployee" class="col s12" action="index.html" method="post">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" name="" value="" v-model="employee_id" required>
            <label for="">Employee ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" name="" value="" v-model="name" required>
            <label for="">Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" name="" value="" v-model="dept" required>
            <label for="">Department</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" name="" value="" v-model="position" required>
            <label for="">Position</label>
          </div>
        </div>
        <button type="submit" name="button" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'

export default { name: 'new-employee', data () { return { employee_id: null,
name: null, dept: null, position: null } }, methods: { saveEmployee () {
db.collection('employees').add({ employee_id: this.employee_id, name: this.name,
dept: this.dept, position: this.position }) .then(docRef => {
this.$router.push('/'); }) .catch(error => console.log(err)) } } } </script>

<style lang="css">
</style>



